import React, { useState, useEffect } from 'react';
import { ICN_COURSE_MENUE, ICN_HEADER_EXPAND, ICN_LANGUAGE, ICN_MORE_FILTER, ICN_NEXT, ICN_SEARCH, ICN_TOTAL_LECTURE, ICN_TOTAL_TIME, IMG_CARD } from '../../../Components/Common/Icon';
import RestService from '../../../Services/api.service';
import { Link } from 'react-router-dom';
import DropdownItem from '../../../Components/Common/DropdownItem';
import SkeletonLoader from '../../../Components/Common/SkeletonLoader';

const Library = () => {
  const [isVisible, setIsVisible] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [filterLanguage, setFilterLanguage] = useState('');
  const [allCourses, setAllCourses] = useState([]);
  const [categoriesSubCategories, setCategoriesSubCategories] = useState([]);
  const [visibleCategories, setVisibleCategories] = useState([]);
  const [loader, setLoader] = useState(true);
  const [filterPriceRating, setFilterPriceRating] = useState('');

  let user = localStorage.getItem('user')
    ? JSON.parse(localStorage.getItem('user'))
    : '';

  const toggleVisibility = () => {
    setIsVisible(!isVisible);
  };

  const toggleCategories = (categorySid) => {
    if (visibleCategories.includes(categorySid)) {
      setVisibleCategories(visibleCategories.filter((sid) => sid !== categorySid));
    } else {
      setVisibleCategories([...visibleCategories, categorySid]);
    }
  }

  const resetRadioInputs = () => {
    const radioInputs = document.querySelectorAll('input[type="radio"]');
    radioInputs.forEach((input) => {
      input.checked = false;
    });
  };

  const handleDetailsToggle = () => {
    // Set filterPriceRating to an empty string when details tab is closed
    if (!document.getElementById('details').open) {
      setFilterPriceRating('');
      resetRadioInputs();
    }
  };

  const handleFilterChange = (value) => {
    setFilterPriceRating(value);
    resetRadioInputs();
  };

  const callFilterApi = (filterValue) => {
    if ('user' in localStorage) {
      getCosumerCoursesForUserFilterPriceRating(filterValue);
    }
    else {
      getCosumerCoursesForFilterPriceRating(filterValue);
    }
  }

  const handleCourseSid = (courseSid) => {
    localStorage.setItem('courseSid', courseSid)
  }



  //get all courses
  const getCosumerCourses = () => {
    try {
      // spinner.show();
      // const subCategorySid = subCategorySid;
      RestService.getCosumerCourses().then(
        response => {
          if (response.status === 200) {
            setAllCourses(response.data);
            setTimeout(() => {
              setLoader(false);
            }, 2000);

          }

        },
        err => {
          console.error(err);
        }
      )
    } catch (err) {
      console.error("error occur on getCosumerCourses()", err)
    }
  }

  //get all courses for user
  const getCosumerCoursesForUser = () => {
    try {
      // spinner.show();
      // const subCategorySid = subCategorySid;
      const consumerSid = user?.sid;
      RestService.getCosumerCoursesForUser(consumerSid).then(
        response => {
          if (response.status === 200) {
            setAllCourses(response.data);
            setTimeout(() => {
              setLoader(false);
            }, 2000);

          }
        },
        err => {
          console.error(err);
        }
      )
    } catch (err) {
      console.error("error occur on getCosumerCoursesForUser()", err)
    }
  }

  //filter based on subcategory or category
  const getCosumerCoursesWithFilterCategorySubCategory = (categorySid, subCategorysid) => {
    try {
      // spinner.show();
      // const subCategorySid = subCategorySid;
      RestService.getCosumerCoursesWithFilterCategorySubCategory(categorySid, subCategorysid).then(
        response => {
          setAllCourses(response.data);
        },
        err => {
          console.error(err);
        }
      )
    } catch (err) {
      console.error("error occur on getCosumerCoursesWithFilterCategorySubCategory()", err)
    }
  }

  const getCosumerCoursesWithFilterCategory = (categorySid) => {
    try {
      // spinner.show();
      // const subCategorySid = subCategorySid;
      RestService.getCosumerCoursesWithFilterCategory(categorySid).then(
        response => {
          setAllCourses(response.data);
        },
        err => {
          console.error(err);
        }
      )
    } catch (err) {
      console.error("error occur on getCosumerCoursesWithFilterCategory()", err)
    }
  }

  //filter based on subcategory or category for user
  const getCosumerCoursesWithFilterCategorySubCategoryForUser = (categorySid, subCategorysid) => {
    try {
      // spinner.show();
      // const subCategorySid = subCategorySid;
      const consumerSid = user?.sid;
      RestService.getCosumerCoursesWithFilterCategorySubCategoryForUser(categorySid, subCategorysid, consumerSid).then(
        response => {
          setAllCourses(response.data);
        },
        err => {
          console.error(err);
        }
      )
    } catch (err) {
      console.error("error occur on getCosumerCoursesWithFilterCategorySubCategoryForUser()", err)
    }
  }

  const getCosumerCoursesWithFilterCategoryForUser = (categorySid) => {
    try {
      // spinner.show();
      // const subCategorySid = subCategorySid;
      const consumerSid = user?.sid;
      RestService.getCosumerCoursesWithFilterCategoryForUser(categorySid, consumerSid).then(
        response => {
          setAllCourses(response.data);
        },
        err => {
          console.error(err);
        }
      )
    } catch (err) {
      console.error("error occur on getCosumerCoursesWithFilterCategoryForUser()", err)
    }
  }

  //get sidebar for all categorises and subcategories
  const getAllCategoriesAndSubCategories = () => {
    try {
      // spinner.show();
      // const subCategorySid = subCategorySid;
      RestService.getAllCategoriesAndSubCategories().then(
        response => {
          setCategoriesSubCategories(response.data);
        },
        err => {
          console.error(err);
        }
      )
    } catch (err) {
      console.error("error occur on getAllCategoriesAndSubCategories()", err)
    }
  }

  //get filter courses for price or rating
  const getCosumerCoursesForFilterPriceRating = (value) => {
    try {
      let payload = {};
      if (value === 'priceLowToHigh') {
        payload.priceFilter = -1;
        payload.ratingFilter = 0;
      }
      else if (value === 'priceHighToLow') {
        payload.priceFilter = 1;
        payload.ratingFilter = 0;
      }
      else if (value === 'ratingLowToHigh') {
        payload.priceFilter = 0;
        payload.ratingFilter = -1;
      }
      else {
        payload.priceFilter = 0;
        payload.ratingFilter = 1;
      }
      RestService.getCosumerCoursesForFilterPriceRating(payload).then(
        response => {
          if (response.status === 200) {
            setAllCourses(response.data);
            setTimeout(() => {
              setLoader(false);
            }, 2000);

          }

        },
        err => {
          console.error(err);
        }
      )
    } catch (err) {
      console.error("error occur on getCosumerCoursesForFilterPriceRating()", err)
    }
  }

  const getCosumerCoursesForUserFilterPriceRating = (value) => {
    try {
      // spinner.show();
      // const subCategorySid = subCategorySid;
      const consumerSid = user?.sid;
      let payload = {};
      if (value === 'priceLowToHigh') {
        payload.priceFilter = -1;
        payload.ratingFilter = 0;
      }
      else if (value === 'priceHighToLow') {
        payload.priceFilter = 1;
        payload.ratingFilter = 0;
      }
      else if (value === 'ratingLowToHigh') {
        payload.priceFilter = 0;
        payload.ratingFilter = -1;
      }
      else {
        payload.priceFilter = 0;
        payload.ratingFilter = 1;
      }
      RestService.getCosumerCoursesForUserFilterPriceRating(consumerSid, payload).then(
        response => {
          if (response.status === 200) {
            setAllCourses(response.data);
            setTimeout(() => {
              setLoader(false);
            }, 2000);

          }

        },
        err => {
          console.error(err);
        }
      )
    } catch (err) {
      console.error("error occur on getCosumerCoursesForUserFilterPriceRating()", err)
    }
  }

  useEffect(() => {
    if ('user' in localStorage) {
      getCosumerCoursesForUser();
    }
    else {
      getCosumerCourses();
    }
    getAllCategoriesAndSubCategories();
  }, []);


  return (
    <>

      <div className='course-container'>
        <div className='row '>
          {/*  filter options */}
          <div className='col-lg-3 col-md-3 col-sm-12 mainsidebar mb-5' >
            <div className='filter-sidebar-main p-5' style={{ height: "100%" }}>
              {
                categoriesSubCategories.map((category) => {
                  return (
                    <>
                      <div >
                        <div className='filter-sidebar-list' onClick={() => {
                          if ('user' in localStorage) {
                            getCosumerCoursesWithFilterCategoryForUser(category.categorySid);
                          }
                          else {
                            getCosumerCoursesWithFilterCategory(category.categorySid);
                          }
                        }}>
                          {/* Job Role Path */}
                          {/* {category.categoryName} */}
                          {

                            loader ?
                              <SkeletonLoader skeletonCount={1} />
                              :

                              <DropdownItem title={category.categoryName} theme="dark">
                                {
                                  category.subCategories.map((subcategory) => {
                                    return (
                                      <div className='filter-sidebar-submenu '
                                        onClick={(e) => {
                                          if ('user' in localStorage) {
                                            e.stopPropagation(); // Stop the event propagation
                                            getCosumerCoursesWithFilterCategorySubCategoryForUser(category.categorySid, subcategory.subCategorySid);
                                          }
                                          else {
                                            e.stopPropagation(); // Stop the event propagation
                                            getCosumerCoursesWithFilterCategorySubCategory(category.categorySid, subcategory.subCategorySid);
                                          }

                                        }}>
                                        {ICN_COURSE_MENUE}  {subcategory.subCategoryName.charAt(0) + subcategory.subCategoryName.substring(1).toLowerCase()}
                                      </div>
                                    )
                                  })
                                }
                              </DropdownItem>
                          }
                          {/* {category.categoryName.charAt(0) + category.categoryName.substring(1).toLowerCase()} */}
                        </div>
                      </div>
                    </>

                  )
                })
              }

            </div>
          </div>


          {/* course filter  */}

          <div className='col-lg-9 col-md-9 col-sm-12'>
            <div className='container'>
              <div className='container'>
                <div className='library-header mb-5 mt-5'>

                  <div className='row'>


                    <div className='main-header-title pt-3'>{
                      loader ?
                        <SkeletonLoader skeletonCount={1} />
                        : "DevOps Training Library"}</div>
                    <div className='main-header-sub-title pt-5' >
                      {
                        loader ?
                          <SkeletonLoader skeletonCount={1} />
                          :
                          `Learn DevOps and build your skills with the DevOps tools that matter - Puppet, Docker, Chef, Ansible, and more.
                      Train with scenario-based Learning Paths and thousands of Courses, Quizzes, and Hands-on Labs. New content added and updated weekly.`
                      }
                    </div>

                  </div>


                </div>
              </div>


              {/* serach */}

              <div className='container'>
                <div className='row  align-items-center' style={{ justifyContent: "space-between" }}>
                  <div className='col-md-8 col-sm-12'>
                    {/* Content for More Filter goes here */}
                    {/* <details id="details" onToggle={handleDetailsToggle}>
                      <summary className='morefilter'>{ICN_MORE_FILTER}<span>More Filter</span></summary>
                      <div onClick={() => handleFilterChange('PRICE')} style={{ cursor: 'pointer' }}>
                        Price
                      </div>
                      <div onClick={() => handleFilterChange('RATING')} style={{ cursor: 'pointer' }}>
                        Rating
                      </div>
                      {
                        filterPriceRating === 'PRICE' ?
                          <div>
                            
                            <div>
                              <input type="radio" id="priceLowToHigh" name="price" value="priceLowToHigh" onChange={(e) => callFilterApi(e.target.value)} />
                              <label for="dewey">Price Low to High</label>
                            </div>

                            <div>
                              <input type="radio" id="priceHighToLow" name="price" value="priceHighToLow" onChange={(e) => callFilterApi(e.target.value)} />
                              <label for="louie">Price High to Low</label>
                            </div>
                          </div>
                          :
                          filterPriceRating === 'RATING' &&
                          <div>
                          
                            <div>
                              <input type="radio" id="ratingLowToHigh" name="rating" value="ratingLowToHigh" onChange={(e) => callFilterApi(e.target.value)} />
                              <label for="dewey">Rating Low to High</label>
                            </div>

                            <div>
                              <input type="radio" id="ratingHighToLow" name="rating" value="ratingHighToLow" onChange={(e) => callFilterApi(e.target.value)} />
                              <label for="louie">Rating High to Low</label>
                            </div>
                          </div>

                      }


                    </details>  */}
                    {
                      loader ?
                        <SkeletonLoader skeletonCount={0.5} />
                        :
                        <button className='morefilter' onClick={toggleVisibility}>{ICN_MORE_FILTER} More Filter</button>
                    }

                  </div>
                  <div className='col-md-4 col-sm-12'>
                    {/* Content for Search goes here */}
                    {
                      loader ?
                        <SkeletonLoader skeletonCount={1} />
                        :
                        <div class="d-flex ">
                          <div class="input-group mt-4 ">
                            <input
                              type="text"
                              className='form-control applycoupn'
                              placeholder="Search Courses"
                              value={searchQuery}
                              onChange={(e) => setSearchQuery(e.target.value)}

                            />
                            <button class=" applycoupn-btn" type="button" id="button-addon1" data-mdb-ripple-color="dark">
                              {ICN_SEARCH}
                            </button>
                          </div>
                        </div>
                    }

                  </div>
                </div>
              </div>
              {/* EXPLORE MORE COURSE */}

              <div className='container'>
                {allCourses.length === 0 ? (
                  <div className='text-center mt-5'>
                    <h4>No results found.</h4>
                  </div>
                ) : (
                  <div class="row mt-5 mb-5">

                    {
                      allCourses.map((item) => {
                        return (
                          <div class="col-12 col-sm-12 col-md-6 col-lg-4 mb-3">
                            <div class="card py-1 px-5 h-100 ">

                              {/* {item.headerimg} */}
                              <div class="card-body">
                                {
                                  loader ?
                                    <SkeletonLoader skeletonCount={1} />
                                    :
                                    item.courseType === 'INSTRUCTOR_LED' &&
                                    <>
                                      <div className="ribbon-2 ">
                                        <div className='text-center instled'>Instructor Led</div>
                                        <div className='instled-date pb-1'>{loader ?
                                          <SkeletonLoader skeletonCount={1} />
                                          : item?.duration !== "NO_EXPIRY" && item.duration} </div>

                                      </div>


                                    </>
                                }

                                <h4 class="card-title mt-5 pt-4">{
                                  loader ?
                                    <SkeletonLoader skeletonCount={1} />
                                    :
                                    item.courseName.length >= 25 ? `${item.courseName.substring(0, 25)}...` : item.courseName
                                }</h4>
                                {
                                  loader ?
                                    <SkeletonLoader skeletonCount={1} />
                                    :

                                    <div className='d-flex gap-3 pt-3'>
                                      <button className='card-top-btn'>{item.categoryName}</button>
                                      <button className='card-top-btn'>{item.subCategoryName}</button>
                                      {
                                        item.tags.map((tag) => {
                                          return (
                                            <button className='card-top-btn w-100'>{tag}</button>
                                          )
                                        })
                                      }

                                    </div>
                                }
                                <div className='d-flex gap-5 mb-4 mt-5'>
                                  <div className='total card-subtitle'><span className='mx-2'>{ICN_TOTAL_LECTURE}</span><span className='mt-5'>{
                                    loader ?
                                      <SkeletonLoader skeletonCount={1} />
                                      :
                                      item.totalContent} Lectures</span></div>
                                  <div className='total card-subtitle'> <span className='mx-2'>{ICN_TOTAL_TIME}</span> <span className='mt-5'>{
                                    loader ?
                                      <SkeletonLoader skeletonCount={1} />
                                      : item.totalDuration} Total Hours</span></div>
                                </div>
                                <p class="card-text gap-2"><span >{ICN_LANGUAGE}</span>{
                                  loader ?
                                    <SkeletonLoader skeletonCount={1} />
                                    : item.languages.toString()} </p>

                                {
                                  loader ?
                                    <SkeletonLoader skeletonCount={1} />
                                    :
                                    <Link to={`/course/${item.courseName}`} onClick={() => handleCourseSid(item.courseSid)} state={{ some: item }} class="card-view-course">View Course {ICN_NEXT}</Link>
                                }


                              </div>
                            </div>
                          </div>
                        )
                      })
                    }


                  </div>
                )}
              </div>
            </div>



          </div>



        </div>
      </div>

    </>
  )
}

export default Library