import React, { useState } from 'react';
import { ICN_CHROME, ICN_HEADER_LOGO1, ICN_LOGO, ICN_NEXT, NEXT } from '../../Components/Common/Icon';
import "./Auth.css";
import { Link } from 'react-router-dom';
import RestService from '../../Services/api.service';
import AxiosService from '../../Services/axios.service';
import { TokenService } from '../../Services/storage.service';
import moment from 'moment';
import { useNavigate } from 'react-router-dom';
import ToastMessage from '../../Store/ToastHook';
import { ToastContainer } from 'react-toastify';
import CryptoJS from 'crypto-js';

const SignupForCart = ({ setShowSignUp, addToCart }) => {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [jobtitle, setJobTitle] = useState('');
    const [phone, setPhone] = useState('');
    const [about, setAbout] = useState('');
    const [showLogin, setShowLogin] = useState(false);

    const [agree, setAgree] = useState(false);
    const [emailLogin, setEmailLogin] = useState('');
    const [passwordLogin, setPasswordLogin] = useState('');
    const navigate = useNavigate();
    const secretKey = process.env.REACT_APP_SECRET;

    // on login the user
    const onLogin = () => {
        const type = 'CONSUMER';
        // const emailEncrypt = CryptoJS.AES.encrypt(emailLogin, secretKey).toString();
        const passwordEncrypt = CryptoJS.AES.encrypt(passwordLogin, secretKey).toString();

        const payload = {
            'email': emailLogin,
            'password': passwordEncrypt
        }
        try {
            RestService.login(payload, type).then(
                response => {
                    if (response.status === 200) {
                        let data = response.data

                        // setTimeout(() => {
                        //     addToCart();
                        // }, 2000);

                        localStorage.setItem('user', JSON.stringify(data));
                        localStorage.setItem("timestamp", moment().format());
                        AxiosService.init('', response.data?.jwtToken);
                        TokenService.saveToken(response.data?.jwtToken);
                        setShowSignUp(false);
                        addToCart();

                    }




                },
                err => {


                    if (err.request.status === 0) {

                        ToastMessage({ type: "error", message: 'Please Check Your Network Connection', time: 2500 });

                    }
                    else if (err.response.status === 401) {

                        ToastMessage({ type: "error", message: 'Invalid User Name / Password!', time: 2500 });

                    }
                    else if (err.response.status === 500) {

                        ToastMessage({ type: "error", message: `${err.response.data.message}`, time: 2500 });

                    }
                    else {

                        ToastMessage({ type: "error", message: 'User with email already exist', time: 2500 });
                    }

                }
            ).finally(() => {
                // spinner.hide();
            });
        } catch (err) {
            // Toast.error({ message: 'Invalid User Name / Password!' })
            console.error("Error occured on login page", err)
        }
    }

    const handleLoginSignup = () => {
        setShowLogin(!showLogin);
    }

    const autoLogin = async () => {
        try {
            const emailEncrypt = CryptoJS.AES.encrypt(email, secretKey).toString();
            const firstName = CryptoJS.AES.encrypt(name, secretKey).toString();
            const hearAboutUs = CryptoJS.AES.encrypt(about, secretKey).toString();
            const jobTitle = CryptoJS.AES.encrypt(jobtitle, secretKey).toString();
            const passwordEncrypt = CryptoJS.AES.encrypt(password, secretKey).toString();
            const phoneNumber = CryptoJS.AES.encrypt(phone, secretKey).toString();
            let payload = {
                "email": emailEncrypt,
                "firstName": firstName,
                "hearAboutUs": hearAboutUs,
                "jobTitle": jobTitle,
                "password": passwordEncrypt,
                "phoneNumber": phoneNumber
            }
            const response = await RestService.autoLogin(payload);

            let data = response.data;
            setAbout('');
            setEmail('');
            setJobTitle('');
            setName('');
            setPassword('');
            setPhone('');
            localStorage.setItem('user', JSON.stringify(data));
            localStorage.setItem("timestamp", moment().format());
            AxiosService.init('', response.data?.jwtToken);
            TokenService.saveToken(response.data?.jwtToken);
            setShowSignUp(false);
            addToCart();
        } catch (err) {
            if (err.request.status === 0) {

                ToastMessage({ type: "error", message: 'Please Check Your Network Connection', time: 2500 });

            }
            else if (err.response.status === 401) {

                ToastMessage({ type: "error", message: 'Invalid User Name / Password!', time: 2500 });

            }
            else if (err.response.status === 500) {

                ToastMessage({ type: "error", message: `${err.response.data.message}`, time: 2500 });

            }
            else {

                ToastMessage({ type: "error", message: 'User with email already exist', time: 2500 });
            }

            console.error();
        }
    };
    return (
        <>
            <ToastContainer />
            {
                showLogin ?

                    <div class="signup-form">

                        <div className='text-center'>
                            {ICN_LOGO}
                        </div>
                        <h2 className='pt-4'>Create an account</h2>
                        <p className='pt-3'>Explore courses across DevOps, Cloud and more domains</p>
                        {/* <hr/> */}

                        <div class="form-group pt-5">
                            <label> Name</label>
                            <div class="input-group">
                                {/* <div class="input-group-prepend">
					<span class="input-group-text">
						<span class="fa fa-user"></span>
					</span>                    
				</div> */}
                                <input type="text" onChange={(e) => setName(e.target.value)} value={name} class="form-control" name="username" placeholder="Enter your Name" required="required" />
                            </div>
                        </div>



                        <div class="form-group">
                            <label>Email</label>
                            <div class="input-group">
                                {/* <div class="input-group-prepend">
					<span class="input-group-text">
						<i class="fa fa-paper-plane"></i>
					</span>                    
				</div> */}
                                <input type="email" onChange={(e) => setEmail(e.target.value)} value={email} class="form-control" name="email" placeholder="Email Address" required="required" />
                            </div>
                        </div>
                        <div class="form-group">
                            <label>Password</label>
                            <div class="input-group">
                                {/* <div class="input-group-prepend">
					<span class="input-group-text">
						<i class="fa fa-lock"></i>
					</span>                    
				</div> */}

                                <input type="password" onChange={(e) => setPassword(e.target.value)} value={password} class="form-control" name="password" placeholder="Password" required="required" />
                            </div>
                        </div>
                        <div className='row'>
                            <div className='col-md-6 col-lg-6 col-sm-12'>
                                <div class="form-group ">
                                    <label>Job Title</label>
                                    <div class="input-group">
                                        {/* <div class="input-group-prepend">
					<span class="input-group-text">
						<span class="fa fa-user"></span>
					</span>                    
				</div> */}
                                        <input type="text" onChange={(e) => setJobTitle(e.target.value)} value={jobtitle} class="form-control" name="username" placeholder="Job Title" required="required" />
                                    </div>
                                </div>
                            </div>

                            <div className='col-md-6 col-lg-6 col-sm-12'>
                                <div class="form-group ">
                                    <label>Phone Number</label>
                                    <div class="input-group">
                                        {/* <div class="input-group-prepend">
					<span class="input-group-text">
						<span class="fa fa-user"></span>
					</span>                    
				</div> */}
                                        <input type="text" onChange={(e) => setPhone(e.target.value)} value={phone} class="form-control" name="username" placeholder="Enter your Number" required="required" />
                                    </div>
                                </div>
                            </div>
                        </div>


                        <div class="form-group ">
                            <label>How did you hear about us</label>
                            <div class="input-group">
                                {/* <div class="input-group-prepend">
					<span class="input-group-text">
						<span class="fa fa-user"></span>
					</span>                    
				</div> */}
                                <input type="text" onChange={(e) => setAbout(e.target.value)} value={about} class="form-control" name="username" placeholder="How did you hear about us" required="required" />
                            </div>
                        </div>





                        {/* <div class="form-group">
			<div class="input-group">
				<div class="input-group-prepend">
					<span class="input-group-text">
						<i class="fa fa-lock"></i>
						<i class="fa fa-check"></i>
					</span>                    
				</div>
				<input type="text" class="form-control" name="confirm_password" placeholder="Confirm Password" required="required"/>
			</div>
        </div> */}
                        {/* <div class="form-group">
			<label class="form-check-label"><input type="checkbox" required="required"/> I accept the <a href="#">Terms of Use</a> &amp; <a href="#">Privacy Policy</a></label>
		</div> */}
                        <div class="form-group">
                            <button type="submit" class="btn btn-primary btn-lg" onClick={autoLogin}>Get started</button>
                        </div>


                        <h3>OR</h3>
                        <div class="form-group">
                            <button type="submit" class="btn-login  btn-lg">{ICN_CHROME} Sign up with Google</button>
                        </div>





                        <div class="text-center alredyhv">Already have an account? <button style={{
                            border: "none", background: "transparent", color: "#6941c6"
                        }} onClick={handleLoginSignup} >Login here</button></div>



                    </div>
                    :
                    <>
                        <div className='row'>
                            {/* normal user */}

                            <div className='col-md-6 col-lg-6 col-xl-6 col-sm-12 '>
                                <div class="signup-form" style={{
                                    borderRadius: "12px",
                                    background: "var(--Primary-50, #F9F5FF)", padding: "32px"
                                }}>
                                    {/* <form > */}
                                    {/* <div className='text-center'>
                                {ICN_LOGO}
                            </div> */}
                                    <h2 className='pt-4'>Already have an account? Log in here.</h2>
                                    <p className='pt-3'>Welcome back! Please enter your details.</p>
                                    {/* <hr/> */}

                                    <div class="form-group">
                                        <label>Email</label>
                                        <div class="input-group">
                                            {/* <div class="input-group-prepend">
                            <span class="input-group-text">
                                <i class="fa fa-paper-plane"></i>
                            </span>                    
                        </div> */}
                                            <input type="email" class="form-control" name="email" placeholder="Email Address" onChange={(e) => setEmailLogin(e.target.value)} required="required" />
                                        </div>
                                    </div>
                                    <div class="form-group">
                                        <label>Password</label>
                                        <div class="input-group">
                                            {/* <div class="input-group-prepend">
                            <span class="input-group-text">
                                <i class="fa fa-lock"></i>
                            </span>                    
                        </div> */}

                                            <input type="password" class="form-control" name="password" placeholder="Password" onChange={(e) => setPasswordLogin(e.target.value)} required="required" />
                                        </div>
                                    </div>
                                    {/* <div class="form-group">
                    <div class="input-group">
                        <div class="input-group-prepend">
                            <span class="input-group-text">
                                <i class="fa fa-lock"></i>
                                <i class="fa fa-check"></i>
                            </span>                    
                        </div>
                        <input type="text" class="form-control" name="confirm_password" placeholder="Confirm Password" required="required"/>
                    </div>
                </div> */}
                                    {/* <div class="form-group">
                    <label class="form-check-label"><input type="checkbox" required="required"/>
                    
                     I accept the <a href="#">Terms of Use</a> &amp; <a href="#">Privacy Policy</a>
                    
                    
                    </label>
                </div> */}
                                    {/* <div class="form-group" style={{ justifyContent: "space-between" }}>
                                <label class="form-check-label"><input type="checkbox" required="required" /> Remember me</label>
                                <Link to="/forgotpassword" style={{ float: "right" }}>Forgot Password</Link>
                            </div> */}
                                    <div class="form-group">
                                        <button type="submit" class="btn btn-primary btn-lg" onClick={onLogin}>Sign in</button>
                                    </div>


                                    <h3>OR</h3>
                                    <div class="form-group">
                                        <button type="submit" class="btn-login  btn-lg">{ICN_CHROME} Sign up with Google</button>
                                    </div>




                                    {/* </form> */}

                                    <div class="text-center alredyhv">Dont have an account?
                                        <button style={{
                                            border: "none", background: "transparent", color: "#6941c6"
                                        }} onClick={handleLoginSignup} >Sign up</button>
                                    </div>



                                </div>
                            </div>

                            {/* guest user */}

                            <div className='col-md-6 col-lg-6 col-xl-6 col-sm-12'>
                                <div class="signup-form">
                                    <form >
                                        {/* <div className='text-center'>
                                {ICN_LOGO}
                            </div> */}
                                        <h2 className='pt-4'>Guest checkout</h2>
                                        <p className='pt-3'>Swiftly secure your digital course. Enter your email for order updates and access. Choose to create an account for future course enrollments in just one click.</p>
                                        {/* <hr/> */}

                                        <div class="form-group">
                                            <label>Name</label>
                                            <div class="input-group">

                                                <input type="text" class="form-control" name="name" placeholder="Full Name" required="required" />
                                            </div>
                                        </div>
                                        <div class="form-group">
                                            <label>Email</label>
                                            <div class="input-group">
                                                {/* <div class="input-group-prepend">
                            <span class="input-group-text">
                                <i class="fa fa-lock"></i>
                            </span>                    
                        </div> */}

                                                <input type="email" class="form-control" name="email" placeholder="Email" required="required" />
                                            </div>
                                        </div>
                                        {/* <div class="form-group">
                    <div class="input-group">
                        <div class="input-group-prepend">
                            <span class="input-group-text">
                                <i class="fa fa-lock"></i>
                                <i class="fa fa-check"></i>
                            </span>                    
                        </div>
                        <input type="text" class="form-control" name="confirm_password" placeholder="Confirm Password" required="required"/>
                    </div>
                </div> */}
                                        <div class="form-group">
                                            <label class="form-check-label"><input type="checkbox" required="required" className='mx-2' />

                                                Create an account with this email for future purchases.


                                            </label>
                                        </div>

                                        <div class="form-group">
                                            <button type="submit" class="btn btn-primary btn-lg">Proceed to payment {NEXT} </button>
                                        </div>






                                    </form>




                                </div>
                            </div>

                        </div>



                    </>

            }

        </>
    )
}

export default SignupForCart