import React, { useState, useEffect } from 'react';
import { ICN_COST_CONTROL, ICN_HANDSON, IMG_COMMENTS, IMG_COST_CONTROL, IMG_EXPLORE_HEADER, IMG_FEATURE, IMG_HANDSON, IMG_SANDBOX_HEADER } from '../../Components/Common/Icon'
import { Link } from 'react-router-dom'
import Readytoexplore from "../../Assets/Images/readytoexplore.png"
import SkeletonLoader from '../../Components/Common/SkeletonLoader'
const Sandbox = () => {
  const [loader, setLoader] = useState(true);
  useEffect(() => {
    setTimeout(() => {
      setLoader(false);
    }, [1000]);
  }, []);
  return (
    <>
      <div >
        <div className='container'>
          <div className='row parrent' style={{ alignItems: "center" }} >
            <div className='col-lg-12 col-md-12 col-sm-12 mt-5 mb-5'>
              {
                loader ?
                  <SkeletonLoader skeletonCount={5} />
                  :
                  <div className='ph' >
                    Create even the most complex
                    <div>sandbox testing environments in a </div>
                    <div>few clicks</div>

                  </div>
              }

              {

                loader ?
                  <SkeletonLoader skeletonCount={2.5} />
                  :
                  <div className='pt mt-5 pb-5'>
                     Were a full-service design and development agency who

                    <div>specialise in simple, useful and beautiful solutions.</div>



                  </div>
              }

              {loader ?
                <SkeletonLoader skeletonCount={0.3} />
                :
                <div className='mt-3'>
                  <Link to="/contactus" class="btn-primary" style={{ padding: "10px 18px" }}>Book a Demo</Link>
                </div>
              }
            </div>
            
              <div className='col-lg-12 col-md-12 col-sm-12 mt-3' >
              {loader ?
              <SkeletonLoader skeletonCount={4.5} />
              :
                IMG_SANDBOX_HEADER
              }
              </div>
            
          </div>

        </div>
      </div>

      <div className='container'>
        <div className='subheading'>Learnlytica for Sandboxing</div>
        <div className='ph mt-5' style={{ fontWeight: "500", textAlign: "center" }}>
          Print (Cloud sandboxes);
          <div>your devs will love you for</div>
        </div>
        <div className='text-center mt-3'>
          <div className='text'>Your DevOps team is critical to your business success  tirelessly working behind

          </div>
          <div className='text'> the scenes to test and ship quality products. But creating, replicating, and</div>
          <div className='text'> managing complex sandbox environments can take ages  and the costs can</div>
          <div className='text'>  really start to add up.</div>
        </div>
      </div>


      {/* fast setup */}

      <div className='layout'>
        <div className='container'>
          <div className='row parrent'>
            <div className='col-md-6 col-lg-6 col-sm-6'>
              <div className='d-flex align-items-center' >
                <div className='icn-bc'>{IMG_COMMENTS}</div>
                <h5 className='mx-5'>FAST SETUP</h5>
              </div>
              <h4 className='mt-3'>Super simple setup</h4>
              <p className='mt-3'>Spin up and tear down environments in minutes, revert to golden images, and make specific changes whenever you need to.</p>
              <div className='sectionpoint mt-3 mb-3'>
                <ul>
                  <li>Create, replicate, and update environments as needed no matter how complicated.
                  </li>
                  <li>No configuration drift for higher quality releases.
                  </li>
                  <li>Explore our instantly deployable VM template library for fully licensed templates.
                  </li>

                </ul>
              </div>

            </div>
            <div className='col-md-6 col-lg-6 col-sm-6'>
              {IMG_FEATURE}
            </div>

          </div>
        </div>
      </div>

      {/* Hands on experience */}

      <div className='container'>
        <div className='row parrent'>
          <div className='col-md-6 col-lg-6 col-sm-6'>
            {IMG_HANDSON}
          </div>
          <div className='col-md-6 col-lg-6 col-sm-6'>
            <div className='d-flex align-items-center' >
              <div className='icn-bc'>{ICN_HANDSON}</div>
              <h5 className='mx-5'>Increase productivity
              </h5>
            </div>
            <h4 className='mt-3'>Increase productivity across teams
            </h4>
            <p className='mt-3'>Accelerate release cycles by quickly reproducing issues and sharing environments and resources anywhere in the world.</p>
            <div className='sectionpoint mt-3 mb-3'>
              <ul>
                <li>Enable multiple dev and QA teams to work in parallel.

                </li>
                <li>Speed up your entire testing process.

                </li>
                <li>Quickly revert your resources whenever you need.

                </li>

              </ul>
            </div>

          </div>


        </div>
      </div>

      {/* cost control */}
      <div className='container'>
        <div className='row parrent'>
          <div className='col-md-6 col-lg-6 col-sm-6'>
            <div className='d-flex align-items-center' >
              <div className='icn-bc'>{ICN_COST_CONTROL}</div>
              <h5 className='mx-5'>COST CONTROL</h5>
            </div>
            <h4 className='mt-3'>Control resource costs
            </h4>
            <p className='mt-3'>Set customizations that auto-suspend or delete environments when they sense inactivity.
            </p>
            <div className='sectionpoint mt-3 mb-3'>
              <ul>
                <li>Reduce lab time and usage costs
                </li>
                <li>Eliminate hardware and cloud sprawl

                </li>


              </ul>
            </div>

          </div>
          <div className='col-md-6 col-lg-6 col-sm-6' >
            {IMG_COST_CONTROL}
          </div>

        </div>
      </div>


      {/* ready to explore */}

      <div className='layout'>
        <div className='container '>
          <div className='row readytoexplore' >
            <div className='col-md-7 leftside ' style={{ padding: "90px 50px" }}>
              <div className='exploretext'>
                Ready to Explore?
              </div>
              <div className='exploretextsub'>
                Request a Demo or Start Your Journey Now!
              </div>
              <div className='mt-5'>
             <Link  to="/contactus"class=" btn_getintouch">Get in Touch</Link>
             </div>
            </div>
            <div className=' col-md-5' style={{ padding: " 0px" }} >
              <img src={Readytoexplore} className='img-fluid' style={{
                float: "inline-end", borderTopRightRadius: "24px",
                borderBottomRightRadius: "24px"
              }} />
            </div>

          </div>
        </div>
      </div>

    </>
  )
}

export default Sandbox